.educathion {
  width: 100vw;
  height: 100vh;
  background-color: $white;
  display: flex;
  flex-direction: column;
  justify-content: start;
  gap: 1vh;
  padding-block: 5vh;
  padding-inline: 5vw 10vw;
  position: relative;
  
  h2 {
    margin: 0;
    color: $red;
    word-wrap: break-word;
    font-size: clamp(1rem, 10vw, 3rem); 
    letter-spacing: 0.01em;
    flex-shrink: 0;
  }
  .text {
    color: $black;
    flex: 1;
    font-size: clamp(1rem, 1.5vw, 1.5rem);
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    p:first-of-type{
      margin-top: 0;
    }
    p:last-of-type{
      margin-bottom: 0;
    }
  }
  .educathion-button{
        border: 3px solid #ED3E2E;
        width: 228px;
        flex-shrink: 0;
        a{
          font-family: 'Outfit', sans-serif;
          font-weight: $bigFontWeight;
          font-size: 1.5remn;
          display: block;
          text-decoration: none;
          color:$red;
          padding: 1rem
        }
      }
}



.motiv{
  width: 100vw;
  height: 100%;
  background-color: $white;
  display: flex;
  flex-direction: column;
  justify-content: start;
  gap: 5vh;
  padding-top: 5vh;
  padding-inline: 5vw 10vw;
  position: relative;
  overflow-y: hidden;
  
  h2 {
    margin: 0;
    color: $red;
    word-wrap: break-word;
    font-size: clamp(1rem, 10vw, 3rem); 
    line-height: 50px;
    letter-spacing: 0.01em;
  }
  .text {
    color: $black;
    padding-inline: 0 0px;
    p:first-of-type{
      padding-bottom: 1vh;
    }
  }
  .educathion-button{
    border: 3px solid #ED3E2E;
    width: 228px;
    a{
      font-family: 'Outfit', sans-serif;
      font-weight: $bigFontWeight;
      font-size: 1.5remn;
      display: block;
      text-decoration: none;
      color:$red;
      padding: 1rem
    }
  }
   
}