.menu-third{
    width: 100%;
    height: 100%;
    // background-color: lightgray;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-inline: 50px;
    gap: 30px;
    margin-top: 5vh;
   h2{
    color:$red;
    font-size: clamp(2rem, 0.7778rem + 1.9753vw, 3rem);
   } 
   p{
    font-size: clamp(1rem, 0.3889rem + 0.9877vw, 1.5rem);
    line-height: 130%;
    max-width: 97%;
    color: $white;
   }
   a{
    font-size: clamp(1rem, 0.3889rem + 0.9877vw, 1.5rem);
    text-transform: uppercase;
    text-decoration: none;
    text-align: center;
    color: $red;
    padding: 10px;
    border: 1px solid $red;
    // width: 180px;
    transition: all 0.5s ease-in-out;
    white-space: nowrap;
    align-self:baseline;
   }
   a:hover{
    color: $white;
    border: 1px solid $white;
   }
}