.main-head{
    width: 100vw;
    display: flex;
    flex-direction: column;
    background-color:$white;
    justify-content: space-evenly;
    overflow-x: hidden;
    position: relative;
    
    .hero-nav{
        list-style: none;
        display: flex;
        justify-content: space-between;
        grid-area: 1 / 1 / 2 / 3; 
        margin-inline: 0;
        padding-inline: 0;
        display: none;
    }
    .head-text{
        h1{
           font-family: 'Outfit';
           font-weight: $boldFontWeight;
            margin: 0;
            color:$red;
            word-wrap: break-word;
            font-size: 40px; 
            // line-height: 50px;
            letter-spacing: 0.01em;
            padding-inline: 5vw 15vw;
        }
        p{
            font-family: 'Outfit', sans-serif;
            font-weight: $smallFontWeight;
            color:$black;
            padding-bottom: 1rem;
            font-size: 1rem; 
            padding-inline: 5vw 10vw;
        };
    }
    .head-logo{
        align-self: center;
        fill: none;
        
        .hero-logo{
          display: block;
          width: 50vw;
          max-height: 50vh;
          stroke: #66666A;
          object-fit: contain;
          stroke-dasharray: 4200;
          stroke-dashoffset: 4200;
          animation: anim 20s forwards linear;
          transition: all 5 ease-in;
        }
    }
  
    
}

@keyframes anim{
       
  to{
    opacity: 1;
      stroke-dashoffset: 0;
      stroke: 0.5 rgb(55, 52, 52);
  }
}



  
  .run-line {
    display: flex;
    flex-wrap: nowrap;
    gap: 1rem;
    animation: marquee 10s  infinite;
    display: inline-block;
    white-space: nowrap;
    
    color:$lightgreen;
    .js-marquee {
      display: inline-block;
      padding-right: 1rem; 
    }
  }
 
  

  
  @keyframes marquee {
    0%{ transform: translateX(+100%); }
    25%{ transform: translateX(-50%); }
    50% { transform: translateX(-100%); }
    75%{ transform: translateX(-150%); }
    100%{ transform: translateX(-200%); }
  }