.contact{
    width: 100vw;
    height: 100%;
    background-color:$white;
    display:flex;
    flex-direction: column;
    justify-content: space-between;
    position: relative;
    .content{
        // margin-top: 5vh;
        height: 85%;
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        padding-inline: 5vw;
        width: calc(100vw - 5vw);
        display: grid; 
        grid-template-columns: 70% 30%; 
        grid-template-rows: 3rem 30vh 1fr; 
        gap: 8% 0px; 
        grid-template-areas: 
          "title title"
          "text links"
          "button button"; 
        h2{
            grid-area: title;
            margin-block:0 ;
            color:$red;
            font-size: clamp(1rem, 10vw, 3rem); 
        }
        .text{
            grid-area: text;
            color:$black;
            max-width: 100%;
            :last-child{
                margin-block: 2vh;
            }
        }
        .beuty-text{
            width: 100vw;
            border-block: 1px solid $gray;
            text-align: center;
            color:$red;
            font-size: 3rem;
            display: none;
           
            h3{
                padding-block: 1rem;
                margin: 0;
            }
        }

        .content-button{
            grid-area: button; 
            justify-self: center;
            height: min-content;
            .button{
                // display: block;
                text-decoration: none;
                color:$red;
                border: 2px solid $red;
                padding-inline: 0.8rem;
                width: 200px;
                text-align: center;
                font-size: 1.2rem;
                font-weight: $boldFontWeight;
            }
        }
        .links{
            justify-self: center;
            grid-area: links;
            width: fit-content;
            display: flex;
            flex-direction: column;
            gap: 20px;
            .link{
                display: flex;
                align-items:center;
                gap: .5rem;
                color:$black;
            }
        }
    }
    footer{
        background-color:#333333;
        max-height: 15%;
        color:lighten($black, 30%);
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        align-items:center;
        padding-block-start: 0;
        p{
            span{
                color:$black;
            }
        }
        .designed{
            text-decoration: none;
            color: goldenrod;
        }
    }
}